import { Component, OnInit } from '@angular/core';
import { OrdersService } from './orders.service';
import { Order } from './order';
import { MenuItem } from 'primeng/primeng';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css'],
  providers: [OrdersService]
})
export class OrdersComponent implements OnInit {
  private orders: Order[];
  private cols: any[];
  private selectedOrders: Order[];
  private items: MenuItem[];
  private display: boolean = false;
  private dialogtext;

  constructor(private ordersService: OrdersService) { }

  ngOnInit() {
    this.ordersService.getOrders().then(orders => this.orders = orders);

    this.items = [
      { label: 'View', icon: 'fa-search', command: (event) => this.menuAction(this.selectedOrders) },
      { label: 'Delete', icon: 'fa-close', command: (event) => this.menuAction(this.selectedOrders) }
    ];

    this.cols = [
      { field: 'guid', header: '系统唯一序号' },
      { field: 'appType', header: '报送类型' },
      { field: 'appTime', header: '报送时间' },
      { field: 'appStatus', header: '业务状态' },
      { field: 'orderType', header: '订单类型' },
      { field: 'orderNo', header: '订单编号' },
      { field: 'ebpCode', header: '电商平台代码' },
      { field: 'ebpName', header: '电商平台名称' },
      { field: 'ebcCode', header: '电商企业代码' },
      { field: 'ebcName', header: '电商企业名称' },
      { field: 'goodsValue', header: '商品价格' },
      { field: 'freight', header: '运杂费' },
      { field: 'discount', header: '非现金抵扣金额' },
      { field: 'taxTotal', header: '代扣税款' },
      { field: 'acturalPaid', header: '实际支付金额' },
      { field: 'currency', header: '币制' },
      { field: 'buyerRegNo', header: '订购人注册号' },
      { field: 'buyerName', header: '订购人姓名' },
      { field: 'buyerIdType', header: '订购人证件类型' },
      { field: 'buyerIdNumber', header: '订购人证件号码' },
      { field: 'payCode', header: '支付企业代码' },
      { field: 'payName', header: '支付企业名称' },
      { field: 'payTransactionId', header: '支付交易编号' },
      { field: 'batchNumbers', header: '商品批次号' },
      { field: 'consignee', header: '收货人姓名' },
      { field: 'consigneeTelephone', header: '收货人电话' },
      { field: 'consigneeAddress', header: '收货地址' },
      { field: 'consigneeDitrict', header: '收货地址行政区划代码' },
      { field: 'note', header: '备注' }
    ];
  }

  menuAction(orders: Order[]) {
    // this.msgs = [];
    // this.msgs.push({severity: 'info', summary: 'Car Selected', detail: car.vin + ' - ' + car.brand});
    this.dialogtext = orders[0];
    this.display = true;

    console.log(this.orders);
  }

  showDialog() {
    this.display = true;
  }
}



